<template>
  <Layout>
    <PageHeader :title="title" :items="items" />
    <div class="row">

      
    <div class="col-md-12">
      <div class="card" style="min-height:700px">
        <div class="p-4 row justify-content-center">
          <div class=" px-3 col-12 row justify-content-center">
            <img :src="user.imagen" width="200px" height="200px" style="border-radius:100%" alt="">
          </div>
          <hr>
          <div class=" px-3 col-12">
            <h3 class="mb-2 nombre">{{user.nombre}} {{user.apellido}}</h3>
            <p>{{user.tipo}}</p>
          </div>
          <button class="btn btn-info m-3" @click="editar()">Editar imagen <i class="ri-image-2-line"></i></button>
          <button class="btn btn-success m-3" @click="setPass()">Editar contrase√±a <i class="ri-lock-password-line"></i></button>
          <div class="col-12">
            <table class="table">
                <tr v-if="user.tipo_tecnico">
                  <th scope="col">Tipo de tecnico</th>
                  <td>{{user.tipo_tecnico}}</td>
                </tr> 
                <tr v-if="user.email">
                  <th scope="col">Email</th>
                  <td>{{user.email}}</td>
                </tr> 
                <tr v-if="user.sexo">
                  <th scope="col">Sexo</th>
                  <td>{{user.sexo}}</td>
                </tr>        
                <tr v-if="user.cargo">
                  <th scope="col">Cargo</th>
                  <td>{{user.cargo}}</td>
                </tr>
                <tr v-if="user.telefono">
                  <th scope="col">Telefono</th>
                  <td>{{user.telefono}}</td>
                </tr>
                <tr v-if="user.direccion">
                  <th scope="col">Direccion</th>
                  <td>{{user.direccion}}</td>
                </tr>  
                <tr v-if="!user.nequi===null||user.nequi">
                  <th scope="col">Nequi</th>
                  <td>{{user.nequi}}</td>
                </tr> 
                <tr v-if="user.nombre_cuenta">
                  <th scope="col">Banco</th>
                  <td>{{user.nombre_cuenta}}</td>
                </tr>  
                <tr v-if="user.cuenta">
                  <th scope="col">Cuenta</th>
                  <td>{{user.cuenta}}</td>
                </tr>                                  
            </table>
          </div>
        </div>
      </div>
      </div>
    </div>
      
      
    









    <b-modal id="modal" false size="lg"  title="Gesti√≥n de usuarios"  hide-footer>
          <ValidationObserver ref="form">
            <b-row  align-v="center" class="m-2">
               <b-col>
                   <div id="preview">
                     <img v-if="url" width="90%" style="float:center!importan" class="rounded"  :src="url" />
                   </div>
                   <b-form-file v-model="file" @change="onFileChange"  class="mt-3" plain></b-form-file>
               </b-col>
            </b-row>        
        </ValidationObserver>

      <button class="btn btn-block btn-success" @click="switchLoc()">Editar Perfil</button>

     </b-modal>



    <b-modal id="modalPass" false size="md"  title="Gesti√≥n de contrase√±a"  hide-footer>
          <ValidationObserver ref="formPass">
            <b-row>
                <b-col>
                  <div class="form-group">
                    <label>Contrase√±a</label>
                    <ValidationProvider name="password" rules="required" v-slot="{ errors }">
                          <input v-model="form.password"  type="password" class="form-control" placeholder=" ">
                          <span style="color:red">{{ errors[0] }}</span>
                    </ValidationProvider>
                  </div>
                </b-col>
            </b-row> 
            <b-row>    
                 <b-col>
                  <div class="form-group">
                    <label>Confirmar contrase√±a</label>
                    <ValidationProvider name="repass" rules="required|confirmed:password" v-slot="{ errors }">
                          <input v-model="form.repass"  type="password" class="form-control" placeholder=" ">
                          <span style="color:red">{{ errors[0] }}</span>
                    </ValidationProvider>
                  </div>


                </b-col>
            </b-row>          
        </ValidationObserver>

        <button class="btn btn-block btn-success" @click="switchLocPass()">Editar Contrase√±a</button>
     </b-modal>
     
  </Layout>
 
</template>

<script>

 import {mapState,mapMutations, mapActions} from 'vuex'
import { ValidationProvider, ValidationObserver } from "vee-validate";
import Layout from "../../layouts/main";
import PageHeader from "@/components/page-header";


/**
 * Dashboard component
 */
export default {
  components: {

    Layout,
    PageHeader,
    ValidationProvider,
    ValidationObserver
  },
  data() {
    return {
      title: "Administracion",
      items: [
        {
          text: "Perfil"
        }
      ],
     dropzoneOptions: {
        thumbnailWidth: 150,
        maxFilesize: 0.5,
        headers: { "My-Awesome-Header": "header value" }
      },
      ver:false,
      url:"",
      url_firma:"",
      modal: true,
      file:null,
      firma:null,
      email: "",
      password: "",
      totalRows: 1,
      currentPage: 1,
      perPage: 10,
      pageOptions: [10, 25, 50, 100],
      filter: null,
      filterOn: [],
      sortBy: "age",
      sortDesc: false,
      fields: ["nombre_tercero","descripcion_tercero","actions"],
      user:[],
      terceros: [], 
      editMode:false,
        form:{
            'id':'',
            'nombre':'',
            'username':'',
            'apellido':'',
            'email':'',
            'telefono':'',
            'tipo':'',
            'sexo':'',
            'entidad':'',
            'cargo':'',
            'codigo':'',
            'foto':'',
            'password':'',
            'repass':'',
            'roles':'',
            
        }
    }
  },
  methods: {
  ...mapActions(['guardarUsuario']),
     ...mapActions(['cerrarSession']),
     salir(){
      localStorage.removeItem('token');
      this.$router.push({ name: 'Home' });
      this.cerrarSession();
     },
     session(){
        if (localStorage.getItem('token')) {
          const token=localStorage.getItem('token');
          this.guardarUsuario(token);
          }else{
          this.$router.push({ name: 'Home' });
          }
        },
    async listarUsers(){
     await this.axios.get('api/user/pefil')
      .then((response) => {
        this.user = response.data;
        console.log(response.data,)
      })
      .catch((e)=>{
        console.log('error' + e);
      })
    },
    editar(){
          this.form.nombre=this.user.nombre;
          this.form.apellido=this.user.apellido;
          this.form.email=this.user.email;
          this.form.telefono=this.user.telefono;
          this.form.tipo=this.user.tipo;
          this.form.sexo=this.user.sexo;
          this.form.entidad=this.user.entidad;
          this.form.cargo=this.user.cargo;
          this.form.codigo=this.user.codigo;
          this.form.imagen=this.user.imagen;
          this.$root.$emit("bv::show::modal", "modal", "#btnShow");
          return;
    },
    setPass(){
          this.$root.$emit("bv::show::modal", "modalPass", "#btnShow");
          return;
    },
    login(){

    },
    switchLoc(){
        this.$refs.form.validate().then(esValido => {
        if (esValido) {
          this.editarUsuario();
        } else {
          
        }
      });
    
   },
   switchLocPass(){
        this.$refs.formPass.validate().then(esValido => {
            if (esValido) {
              this.editarPassword();
            } else {
              this.$swal('Contrase√±as no coinciden!','warning');
            }
          });         
   },
   async editarUsuario(){
      let data = new FormData();
      if (this.file) {
       data.append('filename',this.file);
      }
      var formulario = this.form;
          for (var key in formulario) {
               data.append(key,formulario[key]);
           }
           await this.axios.put('api/user/pefil', data).then(response => {
                  if (response.status==200) {
                    this.listarUsers();
                     this.$swal('Usuario Editardo','','success');
                     this.guardarUsuario(response.data.accessToken);  
                     this.$root.$emit("bv::hide::modal", "modal", "#btnShow");
                          for (var key in formulario) {
                            this.form[key]="";
                          }}
                            }).catch(e => {
                            console.log(e.response.data.menssage);
                            this.$swal('No se pudo editar','','warning');
           });
   },
   async editarPassword(){
      let data = new FormData();
      var formulario = this.form;
          for (var key in formulario) {
               data.append(key,formulario[key]);
           }
           await this.axios.put('auth/recover-password', data).then(response => {
                  if (response.status==200) {
                     this.$swal('Contrase√±a Editada','','success');
                     this.$root.$emit("bv::hide::modal", "modalPass", "#btnShow");
                          for (var key in formulario) {
                            this.form[key]="";
                          }}
                            }).catch(e => {
                            console.log(e.response.data.menssage);
                            this.$swal('No se pudo editar','','warning');
           });
    },
    onFileChange(e) {
      const file = e.target.files[0];
      this.url = URL.createObjectURL(file);
    },
    toggleModal () {
       this.modal = !this.modal
     },
    },
    created(){
        this.session();
        this.listarUsers();
    },
    computed: {
        ...mapState(['usuarioDB']),
    rows() {
       return this.user.length
    },
  },
}
</script>
<style>
  .nombre{
    font-size:2rem;
  }
</style>